/* Slider */

.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}
.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;

    &:focus {
        outline: none;
    }

    &.dragging {
        cursor: pointer;
        cursor: hand;
    }
}
.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;
    margin-left: auto;
    margin-right: auto;

    &:before,
    &:after {
        content: "";
        display: table;
    }

    &:after {
        clear: both;
    }

    .slick-loading & {
        visibility: hidden;
    }
}
.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    [dir="rtl"] & {
        float: right;
    }
    img {
        display: block;
    }
    &.slick-loading img {
        display: none;
    }

    display: none;

    &.dragging img {
        pointer-events: none;
    }

    .slick-initialized & {
        display: block;
    }

    .slick-loading & {
        visibility: hidden;
    }

    .slick-vertical & {
        display: block;
        height: auto;
        border: 1px solid transparent;
    }
}
.slick-arrow.slick-hidden {
    display: none;
}


.slick-slider:not(.slick-dots){
    grid-column-gap: unset !important;
    grid-row-gap: unset !important;
    display: block !important;
    grid-template-columns: unset !important;
    flex-wrap: unset !important;
    display: block !important;
    margin: 0 -10px !important;
    li{
        float: left !important;
        clear: none !important;
        padding: 0 10px !important;
    }
}
.slick-slider .ab-carousel-btn{
    position: absolute;
    top: 50%;
    z-index: 1;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    background: #000;
    opacity: 0.2;
    width: 35px;
    height: 70px;
    cursor: pointer;
    &.prev-item{
        left: 0;
    }
    &.next-item{
        right: 0;
    }
}
.slick-slider ul.slick-dots{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 25px 0 !important;
    li{
        margin: 0 8px;
        display: block;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background: #ccc;
        cursor: pointer;
        padding: 0 !important;
        &.slick-active{
            width: 12px;
            height: 12px;
            background: #000;
        }
        button{
            display: none;
        }
    }
}
.slick-slider:hover{
    .ab-carousel-btn{
        opacity: 0.8;
        visibility: visible;
    }
}
